<template>
  <div>
    <div class="containerBox">
      <!-- 页面标题 -->
      <div class="pageTopic">
        <span class="topicName">处方设置</span>
      </div>

      <div class="formBox">
        <el-form ref="psData" :rules="rules" :model="psParams" label-width="80px">
          <el-form-item label="处方名称" prop="psName">
            <el-input v-model="psParams.psName" placeholder="请输入处方名称"></el-input>
          </el-form-item>
          <el-row>
            <el-col :span="12">
              <el-form-item label="阳极区" prop="psAnode">
                <el-input v-model="psParams.psAnode" placeholder="请输入阳极区"></el-input>
              </el-form-item>
            </el-col>
            <el-col :span="12">
              <el-form-item label="阴极区" prop="psCathode">
                <el-input v-model="psParams.psCathode" placeholder="请输入阴极区"></el-input>
              </el-form-item>
            </el-col>
          </el-row>

          <el-row>
            <el-col :span="12">
              <el-form-item label="电流(μA)" prop="psCurrent">
                <el-input v-model.number="psParams.psCurrent" placeholder="请输入电流">
                  <template slot="append">微安/μA</template>
                </el-input>
              </el-form-item>
            </el-col>
            <el-col :span="12">
              <el-form-item label="时长" prop="psDuration">
                <el-input v-model.number="psParams.psDuration" placeholder="请输入时长">
                  <template slot="append">分钟/min</template>
                </el-input>
              </el-form-item>
            </el-col>
          </el-row>
          <el-divider></el-divider>
          <el-form-item>
            <el-button type="primary" @click="save">保存</el-button>
            <el-button @click="back">返回</el-button>
          </el-form-item>
        </el-form>
      </div>
    </div>
  </div>
</template>
<script>
import AppServerInf from "@/common/serverinf";

export default {
  data() {
    return {
      psParams: {
        psName: '',
        psAnode: '',
        psCathode: '',
        psCurrent: '',
        psDuration: ''
      },

      rules:{
        psName: [{ required: true, message: '请输入处方名称', trigger: 'blur'}],
        psAnode: [{ required: true, message: '请输入阳极区', trigger: 'blur'}],
        psCathode: [{ required: true, message: '请输入阴极区', trigger: 'blur'}],
        psCurrent: [{ required: true, type:'number', message: '请输入电流', trigger: 'blur'}],
        psDuration: [{ required: true, type:'number', message: '请输入时长', trigger: 'blur'}],
      },
    }
  },
  created() {
    const psId = this.$route.params.psId
    if(psId) {
      this.initPsData(psId)
    }
  },
  methods: {
    initPsData(psId){
      this.psParams = this.$db.ps(psId)
    },
    save() {
      this.$refs['psData'].validate(valid=>{
        if (!valid) {
          return false
        }
        //
        this.$db.savePs(this.psParams)
        this.$modal.alert("保存成功")
        //
        this.back()
      })
    }
  }
}
</script>
<style>
</style>



